import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import api from "../utils/api";

export default function ViewPost() {
  const router = useRouter();
  const { id } = router.query;
  const [post, setPost] = useState(null);

  useEffect(() => {
    if (!id) return;
    const loadPost = async () => {
      try {
        const res = await api.get(`/posts/${id}`);
        setPost(res.data);
      } catch (err) {
        console.error(err);
      }
    };
    loadPost();
  }, [id]);

  if (!post) return <p>Loading...</p>;

  return (
    <div className="min-h-screen bg-gray-100 p-6">
      <h2 className="text-3xl font-bold mb-4">{post.title}</h2>
      <p className="text-gray-600 mb-4">{post.description}</p>
      <p className="font-bold text-blue-600 mb-4">ðŸ’° {post.price} BDT</p>

      {/* All Images */}
      {post.images?.map(img => (
        <img key={img} src={`http://localhost:5000/uploads/${img}`} className="w-full max-h-96 object-cover rounded-xl mb-3" />
      ))}

      {/* All Videos */}
      {post.videos?.map(vid => (
        <video key={vid} controls className="w-full max-h-96 rounded-xl mb-3">
          <source src={`http://localhost:5000/uploads/${vid}`} type="video/mp4" />
        </video>
      ))}
    </div>
  );
}
